    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p, ul, li {
    margin: 0;
    padding: 0;
}

textarea {
    resize: none;
}

body {
    display: flex;
}

nav {
    flex: 1;
    padding: 2rem;
    background-color: lightgray;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

ul {
    display: flex;
    flex-direction: column;
}

li {
    list-style-type: none;
    font-size: 1.5rem;
    font-family: Arial, Helvetica, sans-serif;
    padding: 0.5rem;
}

li:hover {
    background-color: gray;
}


.content {
    flex: 4;
    padding: 3rem 5rem;
}

.todo-item {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

circle-progress::part(base) {
    height: 2rem;
    width: 2rem;
    display: flex;
    align-items: center;
}

circle-progress::part(value) {
	stroke-width: 50px;
	stroke: hsl(160, 63%, 55%);
}
circle-progress::part(circle) {
	stroke-width: 50px;
	stroke: #999;
}

.todo-item-title {
    display: flex;
    gap: 1rem;
    align-items: center;
}

.date-div {
    display: flex;
    gap: 2rem;
}

.date-div > img {
    width: 1rem;
}

.todo-item > h2 {
    padding-bottom: 1rem;
    border-bottom: 1px solid black;
}

.item-checklist {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.checklist-item {
    display: flex;
    gap: 1rem;
}


.today {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.today-title {
    margin: 1rem 0;
}

.today-item-checklist {
    display: flex;
    gap: 1rem;
    flex-direction: column;
}

.today-checklist-item {
    font-size: 1.5rem;
}

.today-checklist-item > div > p {
    font-size: 1rem;
    color: darkgray;
}

.image-div {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    margin-top: 3rem;
}

.placeholder {
    width: 25vw;
}

.add-todo > form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.add-todo input, .add-todo textarea {
    border: none;
}

.add-todo input:focus-visible, .add-todo textarea:focus-visible {
    outline: none;
}

input {
    max-width: 35rem;
    text-overflow: clip;
}

form > #title {
    border: none;
    font-size: 2rem;
    font-weight: bold;
}

.subtask {
    padding-left: 1.5rem;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.form-item {
    display: flex;
    gap: 0.5rem;
    align-items: flex-start;
}

.form-item > textarea {
    flex: 1;
}

form > button {
    width: 120px;
    padding: 0.5rem;
}

.projects-div {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
}

.card {
    border-radius: 5px;
    border: 1px solid black;
    padding: 1rem;
    width: 300px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.projects-form label {
    width: 25ch;
    overflow: hidden;
}

.center {
    display: flex;
    justify-content: center;
    align-items: center;
}

dialog > form {
    justify-content: center;
    align-items: center;
    padding: 1rem;
}

dialog > button {
    flex: 0;
}

.project-item {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.project-item-div {
    display: flex;
    justify-content: space-between;
}

.project-item-date {
    min-width: 17ch;
}

.icon {
    width: 2rem;
}

.icon-small {
    width: 1rem;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;AAC1B;;AAEA;IACI,SAAS;IACT,UAAU;AACd;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,OAAO;IACP,aAAa;IACb,2BAA2B;IAC3B,aAAa;IACb,sBAAsB;IACtB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,qBAAqB;IACrB,iBAAiB;IACjB,yCAAyC;IACzC,eAAe;AACnB;;AAEA;IACI,sBAAsB;AAC1B;;;AAGA;IACI,OAAO;IACP,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;AACf;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,aAAa;IACb,mBAAmB;AACvB;;AAEA;CACC,kBAAkB;CAClB,0BAA0B;AAC3B;AACA;CACC,kBAAkB;CAClB,YAAY;AACb;;AAEA;IACI,aAAa;IACb,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,oBAAoB;IACpB,8BAA8B;AAClC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;;AAGA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,sBAAsB;AAC1B;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,uBAAuB;IACvB,mBAAmB;IACnB,gBAAgB;AACpB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;AACf;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,iBAAiB;AACrB;;AAEA;IACI,oBAAoB;IACpB,aAAa;IACb,sBAAsB;IACtB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,WAAW;IACX,uBAAuB;AAC3B;;AAEA;IACI,OAAO;AACX;;AAEA;IACI,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,eAAe;IACf,SAAS;AACb;;AAEA;IACI,kBAAkB;IAClB,uBAAuB;IACvB,aAAa;IACb,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,WAAW;IACX,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,OAAO;AACX;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,WAAW;AACf","sourcesContent":["* {\n    box-sizing: border-box;\n}\n\nh1, h2, h3, h4, h5, h6, p, ul, li {\n    margin: 0;\n    padding: 0;\n}\n\ntextarea {\n 